.service-providers-explorer-root {
    width: 20%;
    height: calc(100vh - 180px);
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-shadow: 0px 0px 5px 2px rgb(240, 240, 240);
}


.hide-button {
    display: none;
}

@media only screen and (max-width: 1200px) {
    .service-providers-explorer-root {
        position: fixed;
        top: 0;
        left: -300px;
        width: 300px;
        height: 100vh;
        transition: left 0.3s ease;
        background-color: white;
        z-index: 996;
        overflow-y: scroll;
    }

    .service-providers-explorer-root-visible {
        background-color: white;
        position: fixed;
        top: 0;
        left: 0px;
        width: 300px;
        height: 100vh;
        transition: left 0.3s ease;
        z-index: 996;
        overflow-y: scroll;
        box-shadow: 0px 0px 2px 1px rgb(240, 240, 240);
    }

    .hide-button {
        top: 20px;
        left: 20px;
        position: absolute;
        display: flex;
        width: 30px;
        height: 30px;
        cursor: pointer;
    }
}